import Vue from 'vue';
import VueRouter from "vue-router";

Vue.use(VueRouter);


const routes = [
    {path: '/', component: () => import('../../pages/Index')},
    {path: '/viewing-videos/:id?', component: () => import('../../pages/ViewingVideos')},
    {path: '/profile', component: () => import('../../pages/Profile')},
    {path: '/videos', component: () => import('../../pages/Videos')},
    {path: '/subsets', component: () => import('../../pages/Subsets')},
    {path: '/points', component: () => import('../../pages/Points')},
    {path: '/payment', component: () => import('../../pages/Payment')},

    {
        path: '/business/advertises',
        component: () => import('../../pages/business/advertises/AdvertisesIndex'),
        children: [
            {path: 'management', component: () => import('../../pages/business/advertises/AdvertisesManagement')},
            {path: 'upload-video', component: () => import('../../pages/business/advertises/AdvertisesUploadVideo')}
        ]
    },
    {
        path: '/business/store', component: () => import('../../pages/business/store/StoreIndex'),
        children: [
            {path: 'management', component: () => import('../../pages/business/store/StoreManagement')},
            {path: 'new-item', component: () => import('../../pages/business/store/StoreItemForm')},
            {path: 'items', component: () => import('../../pages/business/store/StoreItems')},
            {path: 'reports', component: () => import('../../pages/business/store/StoreReports')},
        ]
    },
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
});
router.afterEach(() => {
    window.$('#right-panel').removeClass('show');
})
export default router;