import Vue from 'vue'
import App from './App.vue'
import Authentication from "@/Authentication";

Vue.config.productionTip = false


Vue.filter('thousandSeparator', function (x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
});

new Vue({
    render: function (h) {
        if (this.user)
            return h(App, {
                props: {user: this.user}
            });
        else
            return h(Authentication, {
                props: {user: this.user}
            });
    },
    data: () => ({user: true})
}).$mount('#app')
